<template>
  <div>
<!--    -->
    <breadcrumb>
      <span slot="b-1"></span>
      <span slot="b-2"></span>
    </breadcrumb>
<!--    -->
    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input placeholder="">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
<!--      -->
      <el-table :data="orderList" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="order_number">
        </el-table-column>
        <el-table-column label="" prop="order_price" width="80px">
        </el-table-column>
        <el-table-column label="" prop="pay_status" width="80px">
          <template v-slot="scope">
            <el-tag type="success" v-if="scope.row.pay_status === '1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send" width="80px">
        </el-table-column>
        <el-table-column label="" prop="create_time" width="200px">
          <template v-slot="scope">
            {{scope.row.create_time | dateFormat}}
          </template>
        </el-table-column>
        <el-table-column label="" width="150px">
          <template v-slot="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showBox"></el-button>
            <el-button type="success" icon="el-icon-location" size="mini" @click="showProgress"></el-button>
          </template>
        </el-table-column>
      </el-table>
<!--      -->
      <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="queryInfo.pagenum"
              :page-sizes="[5, 10, 15]"
              :page-size="queryInfo.pagesize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total"
              background>
      </el-pagination>
    </el-card>
<!--    -->
    <el-dialog
            title=""
            :visible.sync="addressVisible"
            width="50%" @close="addressDialogClosed">
      <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px">
        <el-form-item label="/" prop="address1">
          <el-cascader :options="cityData" v-model="addressForm.address1"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
        <span slot="footer" class="dialog-footer">
    <el-button @click="addressVisible = false"> </el-button>
    <el-button type="primary" @click="addressVisible = false"> </el-button>
  </span>
    </el-dialog>
<!--    -->
    <el-dialog
            title=""
            :visible.sync="progressVisible"
            width="50%">
<!--      -->
      <el-timeline>
        <el-timeline-item
                v-for="(activity, index) in progressInfo"
                :key="index"
                :timestamp="activity.time">
          {{activity.context}}
        </el-timeline-item>
      </el-timeline>
      <span slot="footer" class="dialog-footer">
        <el-button @click="progressVisible = false"> </el-button>
        <el-button type="primary" @click="progressVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import Breadcrumb from "../common/Breadcrumb";
  import cityData from "./citydata";

  export default {
    name: "Order",
    components: {
      Breadcrumb
    },
    data() {
      return {
        queryInfo: {
          query: '',
          pagenum: 1,
          pagesize: 5
        },
        total: 0,
        orderList: [],
        addressVisible: false,
        addressForm: {
          address1: [],
          address2: ''
        },
        addressFormRules: {
          address1: [
            {required: true, message: '/', trigger: 'blur'}
          ],
          address2: [
            {required: true, message: '', trigger: 'blur'}
          ],
        },
        cityData: cityData,
        progressVisible: false,
        progressInfo: []
      }
    },
    created() {
      this.getOrderList()
    },
    methods: {
      async getOrderList() {
        const {data: res} = await this.$http.get('orders', {params: this.queryInfo})
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        console.log(res);
        this.total = res.data.total
        this.orderList = res.data.goods
      },
      handleSizeChange(newSize) {
        this.queryInfo.pagesize = newSize
        this.getOrderList()
      },
      handleCurrentChange(newPage) {
        this.queryInfo.pagenum = newPage
        this.getOrderList()
      },
      // 
      showBox() {
        this.addressVisible = true
      },
      addressDialogClosed() {
        this.$refs.addressFormRef.resetFields()
      },
      async showProgress() {
        const {data: res} = await this.$http.get('/kuaidi/1106975712662')

        if (res.meta.status !== 200) {
          return this.$message.error('!')
        }
        this.progressInfo = res.data
        this.progressVisible = true
        console.log(this.progressInfo);
      }
    }
  }
</script>

<style scoped>
  .el-cascader {
    width: 100%;
  }
</style>