<template>
  <div>
<!--    -->
    <breadcrumb>
      <span slot="b-1"></span>
      <span slot="b-2"></span>
    </breadcrumb>
<!--    -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <div>
            <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getGoodsList">
              <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
            </el-input>
          </div>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddpage"></el-button>
        </el-col>
      </el-row>
<!--      table-->
      <el-table :data="goodsList" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="goods_name" label=""></el-table-column>
        <el-table-column prop="goods_price" label="()" width="103px"></el-table-column>
        <el-table-column prop="goods_weight" label="" width="78px"></el-table-column>
        <el-table-column prop="add_time" label="" width="160px">
          <template v-slot="scope">
            {{scope.row.add_time | dateFormat}}
          </template>
        </el-table-column>
        <el-table-column label="" width="130px">
          <template v-slot="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeById(scope.row.goods_id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="queryInfo.pagenum"
              :page-sizes="[3, 5, 8, 10]"
              :page-size="queryInfo.pagesize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total"
              background>
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
  import Breadcrumb from "../common/Breadcrumb";
  export default {
    name: "List",
    components: {
      Breadcrumb
    },
    data() {
      return {
        queryInfo: {
          query: '',
          pagenum: 1,
          pagesize: 5
        },
        // 
        goodsList: [],
        // 
        total: 0
      }
    },
    created() {
      this.getGoodsList()
    },
    methods: {
      // 
      async getGoodsList() {
        const {data: res} = await this.$http.get('goods', {params: this.queryInfo})
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.goodsList = res.data.goods
        this.total = res.data.total
      },
      // 
      handleSizeChange(newSize) {
        this.queryInfo.pagesize = newSize
        this.getGoodsList()
      },
      handleCurrentChange(newPage) {
        this.queryInfo.pagenum = newPage
        this.getGoodsList()
      },
      async removeById(id) {
        const confirmResult = await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err => err)
        if (confirmResult !== 'confirm') {
          return this.$message.info('')
        }
        const {data: res} = await this.$http.delete(`goods/${id}`)
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getGoodsList()
      },
      goAddpage() {
        this.$router.push('/goods/add')
      }
    }
  }
</script>

<style scoped>

</style>