<template>
  <div>
<!--    -->
    <breadcrumb>
    <span slot="b-1"></span>
    <span slot="b-2"></span>
  </breadcrumb>
<!--    -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog"></el-button>
        </el-col>
      </el-row>
<!--      -->
      <tree-table class="treeTable" :data="cateList"
                  :columns="columns"
                  :selection-type="false"
                  :expand-type="false"
                  show-index index-text="#"
                  border
                  :show-row-hover="false">
<!--        -->
        <template slot="isOk" slot-scope="scope">
          <i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color: lightgreen"></i>
          <i class="el-icon-error" v-else style="color: red"></i>
        </template>
<!--        -->
        <template slot="order" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level === 0" size="mini"></el-tag>
          <el-tag type="success" v-else-if="scope.row.cat_level === 1" size="mini"></el-tag>
          <el-tag type="warning" v-else size="mini"></el-tag>
        </template>
<!--        -->
        <template slot="opt" slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditCateDialog(scope.row.cat_id)"></el-button>
          <el-button type="danger" icon="el-icon-delete"size="mini" @click="removeCateById(scope.row.cat_id)"></el-button>
        </template>
      </tree-table>
<!--      -->
      <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="queryInfo.pagenum"
              :page-sizes="[3, 5, 10, 15]"
              :page-size="queryInfo.pagesize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
      </el-pagination>
    </el-card>
<!--    -->
    <el-dialog
            title=""
            :visible.sync="addCateDialogVisible"
            width="50%"
            @close="addCateDialogClosed"
    >
<!--      -->
      <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="100px">
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
<!--          options  -->
<!--          props  -->
          <el-cascader
                  v-model="selectedKeys"
                  :options="parentCateList"
                  expand-trigger="hover"
                  :props="cascaderProps"
                  @change="parentCateChanged"
                  clearable
                  change-on-select>
          </el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="addCateDialogVisible = false"> </el-button>
    <el-button type="primary" @click="addCate"> </el-button>
  </span>
    </el-dialog>
<!--    -->
    <el-dialog
            title=""
            :visible.sync="editCateDialogVisible"
            width="50%">
      <el-form :model="editForm" :rules="editFormRules" ref="editCateFormRef" label-width="100px">
        <el-form-item label="" prop="cat_name">
          <el-input v-model="editForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="removeCateById"> </el-button>
    <el-button type="primary" @click="editCate"> </el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
  import Breadcrumb from "../common/Breadcrumb";
  export default {
    name: "Categories",
    components: {
      Breadcrumb
    },
    data() {
      return {
        queryInfo: {
          type: 3,
          pagenum: 1,
          pagesize: 5
        },
        // 
        cateList: [],
        // 
        total: 0,
        // table
        columns: [{
          label: '',
          prop: 'cat_name',
        }, {
          label: '',
          // 
          type: 'template',
          // 
          template: 'isOk'
        }, {
          label: '',
          // 
          type: 'template',
          // 
          template: 'order'
        }, {
          label: '',
          // 
          type: 'template',
          // 
          template: 'opt'
        }],
        // 
        addCateDialogVisible: false,
        // 
        addCateForm: {
          // 
          cat_name: '',
          // id
          cat_pid: 0,
          // 
          cat_level: 0
        },
        // 
        addCateFormRules: {
          cat_name: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },
        // 
        parentCateList: [],
        // 
        cascaderProps: {
          value: 'cat_id',
          label: 'cat_name',
          children: 'children'
        },
        // id
        selectedKeys: [],
        editCateDialogVisible: false,
        // 
        editForm: {},
        // 
        editFormRules: {
          cat_name: [
            {required: true, message: '', trigger: 'blur'}
          ]
        }
      }
    },
    created() {
      this.getCateList()
    },
    methods: {
      // 
      async getCateList() {
        const {data: res} = await this.$http.get('categories', {
          params: this.queryInfo
        })
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        // cateList
        this.cateList = res.data.result
        // // 
        this.total = res.data.total
      },
      // pagesize
      handleSizeChange(newSize) {
        this.queryInfo.pagesize = newSize
        this.getCateList()
      },
      // pagenum
      handleCurrentChange(newPage) {
        this.queryInfo.pagenum = newPage
        this.getCateList()
      },
      // 
      showAddCateDialog() {
        // 
        this.getParentCateList()
        // 
        this.addCateDialogVisible = true
      },
      // 
      async getParentCateList() {
        const {data: res} = await this.$http.get('categories', {params: {type: 2}})
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        console.log(res.data);
        this.parentCateList = res.data
      },
      // 
      parentCateChanged() {
        // selectedKeyslength0 
        //
        if (this.selectedKeys.length > 0) {
          // id
          this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
          // 
          this.addCateForm.cat_level = this.selectedKeys.length
          return
        } else {
          // id
          this.addCateForm.cat_pid = 0
          // 
          this.addCateForm.cat_level = 0
        }
      },
      addCate() {
        this.$refs.addCateFormRef.validate(async valid => {
          if (!valid) return
          const {data: res} = await this.$http.post('categories', this.addCateForm)
          if (res.meta.status !== 201) {
            return this.$message.error('')
          }
          this.$message.success('')
          this.getCateList()
          this.addCateDialogVisible = false
        })
        // console.log(this.addCateForm);
      },
      // 
      addCateDialogClosed() {
        this.$refs.addCateFormRef.resetFields()
        this.selectedKeys = []
        this.addCateForm.cat_level = 0
        this.addCateForm.cat_pid = 0
      },
      // 
      async showEditCateDialog(id) {
        const {data: res} = await this.$http.get('categories/' + id)
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.editForm = res.data
        this.editCateDialogVisible = true
      },
      // 
      editCate() {
        this.$refs.editCateFormRef.validate(async valid => {
          if (!valid) return
          const {data: res} = await this.$http.put('categories/' + this.editForm.cat_id,
              {
                cat_name: this.editForm.cat_name
              })
          if (res.meta.status !== 200) return this.$message.error('')
          this.$message.success('')
          this.getCateList()
          this.editCateDialogVisible = false
        })
      },
      async removeCateById(id) {
        const confirmResult = await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err => err)
        if (confirmResult !== 'confirm') {
          return this.$message.info('')
        }
        const {data: res} = await this.$http.delete('categories/' + id)
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getCateList()
      }
    }
  }
</script>

<style scoped>
  .treeTable {
    margin-top: 15px;
  }
  .el-cascader {
    width: 100%;
  }
</style>