<template>
  <div>
<!--    -->
    <breadcrumb>
      <span slot="b-1"></span>
      <span slot="b-2"></span>
    </breadcrumb>
<!--    -->
    <el-card>
      <el-table :data="rightsList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="authName"></el-table-column>
        <el-table-column label="" prop="path"></el-table-column>
        <el-table-column label=""prop="level">
          <template v-slot="scope">
            <el-tag v-if="scope.row.level === '0'"></el-tag>
            <el-tag v-else-if="scope.row.level === '1'" type="success"></el-tag>
            <el-tag v-else-if="scope.row.level === '2'" type="warning"></el-tag>
            <el-tag v-else-if="scope.row.level === '2'" type="warning"></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
  import Breadcrumb from "../common/Breadcrumb";

  export default {
    name: "Rights",
    components: {
      Breadcrumb
    },
    data() {
      return {
        // 
        rightsList: []
      }
    },
    created() {
      this.getRightsList()
    },
    methods: {
      // 
      async getRightsList() {
        const {data: res} = await this.$http.get('rights/list', this.rightsList)
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.rightsList = res.data
      }
    }
  }
</script>

