<template>
  <div>
<!--    -->
    <breadcrumb>
      <span slot="b-1"></span>
      <span slot="b-2"></span>
    </breadcrumb>
<!--    -->
    <el-card>
<!--      -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addRolesDialogVisible = true"></el-button>
        </el-col>
      </el-row>
<!--      -->
      <el-table :data="rolesList" border stripe>
<!--        -->
        <el-table-column type="expand">
          <template v-slot="scope">
            <el-row :class="['vcenter', 'bdbottom', i1 ===0 ? 'bdtop' : '']" v-for="(item1, i1) in scope.row.children" :key="item1.id">
<!--              -->
              <el-col :span="5">
                <el-tag  closable @close="removeRightById(scope.row, item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
<!--              -->
              <el-col :span="19">
<!--                for-->
                <el-row v-for="(item2, i2) in item1.children" :class="[i2 === 0 ? '' : 'bdtop', 'vcenter']"  :key="item2.id">
                  <el-col :span="6">
                    <el-tag type="success" closable @close="removeRightById(scope.row, item2.id)">{{item2.authName}}  </el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag type="warning" v-for="(item3, i3) in item2.children" :key="item3.id" closable @close="removeRightById(scope.row, item3.id)">{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
<!--        -->
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template v-slot="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeRolesById(scope.row.id)"></el-button>
            <el-button type="warning" icon="el-icon-setting" size="mini" @click="showSetRightDialog(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
<!--    -->
    <el-dialog
            title=""
            :visible.sync="editDialogVisible"
            width="40%"
            @close="editDialogClosed"
            >
      <el-form ref="editRolesForm" :model="editRolesForm" :rules="editFormRules" label-width="100px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="editRolesForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="editRolesForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
       <el-button @click="editDialogVisible = false"> </el-button>
       <el-button type="primary" @click="editRolesInfo"> </el-button>
      </span>
    </el-dialog>
<!--    -->
    <el-dialog
            title=""
            :visible.sync="addRolesDialogVisible"
            width="50%">
      <el-form :model="addRolesFrom" :rules="editFormRules" ref="addRolesFormRef" label-width="100px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="addRolesFrom.roleName"></el-input>
        </el-form-item>
      </el-form>
      <el-form :model="addRolesFrom" :rules="editFormRules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="addRolesFrom.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="addRolesDialogVisible = false"> </el-button>
    <el-button type="primary" @click="addRoles"> </el-button>
  </span>
    </el-dialog>
<!--    -->
    <el-dialog
            title=""
            :visible.sync="setRightDialogVisible"
            width="50%" @close="setRightDialogClosed">
<!--      -->
      <el-tree :data="rightsList" :props="rightTreeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" ref="treeRef"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRights"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import Breadcrumb from "../common/Breadcrumb";
  export default {
    name: "Roles",
    components: {
      Breadcrumb
    },
    data() {
      return {
        rolesList: [],
        editRolesForm: {},
        editDialogVisible: false,
        editFormRules: {
          roleName: [
            {required: true, message: '', trigger: 'blur'}
          ],
          roleDesc: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },
        addRolesDialogVisible: false,
        addRolesFrom: {
          roleName: '',
          roleDesc: ''
        },
        // 
        setRightDialogVisible: false,
        // 
        rightsList: [],
        // 
        rightTreeProps: {
          label: 'authName',
          children: 'children'
        },
        defKeys: [],
        // id
        roleId: ''
      }
    },
    created() {
      this.getRolesList()
    },
    methods: {
      // 
      async getRolesList() {
        const {data: res} = await this.$http.get('roles', this.rolesList)
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.rolesList = res.data
      },
      // 
      async showEditDialog(id) {
        const {data: res} = await this.$http.get('roles/' + id)
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.editRolesForm = res.data
        this.editDialogVisible = true
      },
      // 
      editDialogClosed() {
        this.$refs.editRolesForm.resetFields()
      },
      // 
      editRolesInfo() {
        this.$refs.editRolesForm.validate(async vaild => {
          if (!vaild) return
          // 
          const {data: res} = await this.$http.put('roles/' + this.editRolesForm.roleId, this.editRolesForm)
          if (res.meta.status !== 200) {
            this.$message.error('')
          }
          this.$message.success('')
          this.editDialogVisible = false
          this.getRolesList()
        })
      },
      async removeRolesById(id) {
        const confirmResult = await this.$confirm(', ?', '',
            {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }
        ).catch(err => err)
        if (confirmResult !== 'confirm') {
          return this.$message.info('')
        }
        const {data: res} = await this.$http.delete('roles/' + id)
        if (res.meta.status !== 200) {
          this.$message.error('')
        } else if (res.meta.status == 200) {
          this.$message.success('')
        }
        this.getRolesList()
      },
      // 
      addRoles() {
        this.$refs.addRolesFormRef.validate(async valid => {
          if (!valid) return
          const {data: res} = await this.$http.post('roles/', this.addRolesFrom)
          if (res.meta.status !== 201) {
             this.$message.error('')
          }
          this.$message.success('')
          this.addRolesDialogVisible = false
          this.getRolesList()
        })
      },
      // id
      async removeRightById(role, rightId) {
        // 
        const confirmResult = await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err => err)

        // 
        if (confirmResult !== 'confirm') {
          return this.$message.info('')
        }
        const {data:res} = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
        if (res.meta.status !==200) {
          return this.$message.error('')
        }
        role.children = res.data
      },
      // 
      async showSetRightDialog(role) {
        this.roleId = role.id
        // 
        const {data: res} = await this.$http.get('rights/tree');
        if (res.meta.status !== 200) {
          return this.$message.error('!')
        }
        // data
        this.rightsList = res.data

        //id
        this.getLeafKey(role, this.defKeys)

        this.setRightDialogVisible = true
      },
      // iddefKeys
      getLeafKey(node, arr) {
         // nodechildren
        if (!node.children){
          return arr.push(node.id)
        }
        node.children.forEach(item => this.getLeafKey(item, arr))
      },
      // 
      setRightDialogClosed() {
        this.defKeys = []
      },
      // 
      async allotRights() {
        const keys = [
            ...this.$refs.treeRef.getCheckedKeys(),
            ...this.$refs.treeRef.getHalfCheckedKeys()
        ]
        const idStr = keys.join(',')

        const {data: res} = await this.$http.post(`roles/${this.roleId}/rights`, {rids: idStr})
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getRolesList()
        this.setRightDialogVisible = false
      }
    }
  }
</script>

<style scoped>
  .el-tag {
    margin: 7px;
  }
  .bdtop {
    border-top: 1px solid #eee;
  }
  .bdbottom {
    border-bottom: 1px solid #eee;
  }
  .vcenter {
    display: flex;
    align-items: center;
  }
</style>